\@font-face {
  font-family: 'cassannet_plus';
  src:  url('./fonts/cassannet_plus_regular-webfont.woff2') format('woff2'),
        url('./fonts/cassannet_plus_regular-webfont.woff') format('woff');
}

@font-face {
  font-family: 'CoventryGarden';
  src:  url('./fonts/CoventryGarden.ttf') format('truetype');
}

*{ 
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body, #root, .App{
  font-family: 'cassannet_plus', 'Roboto', sans-serif;
}

:root{
  font-size: 10px;
}

.App{
   background-color: #f6f6f6;
   color: #111111;
}

.container{
  width: min(130rem, 85%);
  position: relative;
  padding: 0 4%;
  margin: 0 auto;
}


.flex{
  display: flex;
}

/*///////////////////HEADER///////////////*/


header{
  color: #c79e5f;
  position: fixed;
  z-index: 999;
  top: 0;
  width: 100%;
  backdrop-filter: blur(10px);
  user-select: none;
  transition: all 1s ease;
}

header .header-container{
  max-width: 130rem;
  margin: 0 auto;
  padding: 2rem 5rem;
  display: flex;
}

.logo{
  max-height: 6rem;
  position: fixed;
  padding: 0.5rem 1rem;
  border-radius: 15px;
  transition: all 1s ease;
}

.menu{
  list-style: none;
  display: flex;
  margin: 0 0 0 auto;
  min-height: 6rem;
}

.menu ul{
  list-style: none;
  display: flex;
  vertical-align: middle;
}

.menu ul li{
  font-size: 3rem;
  list-style: none;
  display: flex;
  align-items: center;
}

header nav .web li{
  padding-left: 5rem;
}

header nav ul li a{
  color: #111111;
  cursor: pointer;
  padding: 0 1rem;
  background: linear-gradient(rgba(246,246,246, 0) 50%, #ffcb74 50%);
  transition: all 1s ease;
  background-size: 100% 200%;
}

header nav ul li a:hover {
  background-position: bottom;
}

.mobile-logo{
    position: absolute;
    left: 5%;
    top: 3%;
    z-index: 2;
    max-height: 7vh;
    backdrop-filter: blur(10px);
}

.burger{
    position: absolute;
    right: 5%;
    top: 3%;
    z-index: 2;
    font-size: 4.1vh;
    align-self: center;
    padding: 1rem 2rem;
    border-radius: 15px;
    transition: all 1s ease;
    background-color: #f6f6f6;
}

.mobile-menu{
  z-index: 7;
  left: 3%;
  right: 6%;
  background-color: #f6f6f6;
}

.contact{
  z-index: 6;
  left: 5%;
  right: 5%;
  background-color: #ffcb74;
}

.mobile-menu,
.contact{
   position: fixed;
   flex-direction: column;
   justify-content: center;
   top: 10%;
   bottom: 0;
   padding: 0 2%;
   color: #111111;
   border-radius: 15px 15px 0 0;
   -webkit-box-shadow: 39px -8px 95px -42px rgba(0,0,0,0.75);
   -moz-box-shadow: 39px -8px 95px -42px rgba(0,0,0,0.75);
   box-shadow: 20px -8px 95px -42px rgba(0,0,0,0.75);
   transition: all 0.5s ease;
}

.mobile-menu .arrow-container,
.contact .arrow-container{
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 95%;
}

.mobile-menu .arrow,
.contact .arrow{
  position: absolute;
  left: 80%;
  top: 40%;
  border: solid black;
  border-width: 0 4px 4px 0;
  display: inline-block;
  padding: 1rem;
  transition: all 0.5s ease;
}

.contact .arrow{
  transform: rotate(45deg);
}

.mobile-menu .menu-logo{
  margin: 0 auto;
  max-height: 15rem;
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 15px;
}

.mobile-menu li{
   margin: 5rem auto 5rem auto;
   border-bottom: 1px solid #6f6f6f;
}

.mobile-menu li a{
   font-size: 3rem;
}

.mobile{
  display: none;
}

.click-hide{
  display: none;
  position: fixed;
  left: 0; right: 0; top: 0; bottom: 0;
  z-index: 5;
}

/*///////////////////LANDING///////////////*/

.landing{
  height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 1;
  cursor: default;
  background-image: url("./img/waves.svg");
  background-size: 100%;
  background-position: bottom;
  background-repeat: no-repeat;
}

.landing > div{
  margin: 0 auto;
/*  border: 5px solid #c79e5f;*/
}

.landing div > h1{
  position: relative;
  font-size: 2.5rem;
  color: #2f2f2f;
  margin-left: 0.5rem;
  backdrop-filter: blur(10px);
  user-select: none;
  display: inline-block;
}

.landing div span{
  background-color: #ffcb74;
}


.landing div > h2{
  font-size: 10rem;
  backdrop-filter: blur(10px);
  user-select: none;
  display: inline;
}

.landing div > .button-container{
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 5rem;
}

.landing div > .button-container button{
  border: 2px solid #ffcb74;
  font-size: 2rem;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: bold;
  background: linear-gradient(rgba(246,246,246, 0) 50%, #ffcb74 50%);
  transition: all 1s ease;
  background-size: 100% 200%;
  backdrop-filter: blur(10px);
  cursor: pointer;
}

.landing div > .button-container button:hover{
  background-position: bottom;
}

.landing div .flex{
  margin-top: 1rem;
  justify-content: space-between;
  width: fit-content;
}

.landing nav{
  list-style: none;
  display: flex;
  margin: 0 auto;
  min-height: 6rem;
}

.landing nav ul{
  list-style: none;
  display: flex;
  vertical-align: middle;
  justify-content: space-between;
}

.landing nav ul li{
  font-size: 2rem;
  list-style: none;
  display: flex;
  align-items: center;
}

.landing nav ul li a > *{
  display: inline-block;
  transform-origin: center center;
  color: #111111;
  margin-top: 5rem;
  font-size: 5rem;
  cursor: pointer;
  transition: all 1 ease;
  backface-visibility: hidden;
}

.landing nav ul li a > *:hover{
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  -o-transform: scale(1.2);
  -ms-transform: scale(1.2);
  transform: scale(1.2);
}

.landing div > div a{
  margin: 0 2rem;
}

/*///////////////////PROJECTS///////////////*/

.projects{
  height: 100vh;
  background-color: #111111;
  position: relative;
  display: flex;
  align-items: center;
  z-index: 1;
  cursor: default;
  width: 100%;
}

.project-title{
  color: #c79e5f;
  font-size: 5rem;
}

.projects > div{
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  display: flex;
  align-items: center;
  background-image: radial-gradient( 
    rgba(255, 255, 255, 0.1) 2px,
    transparent 1px );
  background-position: 50% 50%;
  background-size: 3rem 3rem;
}

.projects > div > div{
  display: flex;
  margin-top: 5rem;
  background: rgba(255,255,255,1);
  background: -moz-linear-gradient(left, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 0%, rgba(219,219,219,1) 35%, rgba(209,209,209,1) 72%, rgba(227,227,227,1) 100%);
  background: -webkit-gradient(left top, right top, color-stop(0%, rgba(255,255,255,1)), color-stop(0%, rgba(255,255,255,1)), color-stop(35%, rgba(219,219,219,1)), color-stop(72%, rgba(209,209,209,1)), color-stop(100%, rgba(227,227,227,1)));
  background: -webkit-linear-gradient(left, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 0%, rgba(219,219,219,1) 35%, rgba(209,209,209,1) 72%, rgba(227,227,227,1) 100%);
  background: -o-linear-gradient(left, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 0%, rgba(219,219,219,1) 35%, rgba(209,209,209,1) 72%, rgba(227,227,227,1) 100%);
  background: -ms-linear-gradient(left, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 0%, rgba(219,219,219,1) 35%, rgba(209,209,209,1) 72%, rgba(227,227,227,1) 100%);
  background: linear-gradient(to right, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 0%, rgba(219,219,219,1) 35%, rgba(209,209,209,1) 72%, rgba(227,227,227,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#e3e3e3', GradientType=1 );
  border-radius: 15px;
  align-items: center;
  position: relative;
  flex-wrap: wrap;
}

.projects .img-container{
  max-width: 45%;
  margin: 2.5rem auto;
  position: relative;
}

.projects .img-container:after {  
  content: "";
  height: 70px;
  width: 2px;  
  position: absolute;  
  left: -1px;
  top: 65%;
  transition: top, opacity;
  transition-duration: 600ms;
  transition-timing-function: ease;
  background: linear-gradient(
    transparent,
    #9b7032,
    transparent
  );
  opacity: 0;
}

.projects .img-container:hover:after {
  top: 20%;
  opacity: 1;
}

.projects img{
  display: block;
  margin: 0 auto;
  max-width:100%;
  max-height:100%;
  vertical-align: middle;
  border-radius: 15px;
  -webkit-box-shadow: 39px 42px 95px -42px rgba(0,0,0,0.75);
  -moz-box-shadow: 39px 42px 95px -42px rgba(0,0,0,0.75);
  box-shadow: 39px 42px 95px -42px rgba(0,0,0,0.75);
}

/*///////////////////ABOUT///////////////*/

.about{
  position: relative;
  z-index: 1;
  cursor: default;
  width: 100%;
  background-image: url("./img/waves2.svg");
  background-size: 100%;
  background-position: top;
  background-repeat: no-repeat;
  background-color: #f6f6f6;
}

.about > div{
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  display: flex;
  align-items: center;
}

.about .container{
  min-height: 80%;
  margin-top: 5rem;
  margin-bottom: 5rem;
  display: flex;
  border-radius: 15px;
  justify-content: center;
  position: relative;
  flex-wrap: wrap;
  background-color: #f6f6f6;
  padding: 5rem;
  transition: all 1s ease;
  -webkit-box-shadow: 39px 42px 95px -42px rgba(0,0,0,0.75);
  -moz-box-shadow: 39px 42px 95px -42px rgba(0,0,0,0.75);
  box-shadow: 39px 42px 95px -42px rgba(0,0,0,0.75);
}

.about .container .about-head{
  width: 100%;
}

.about .container .about-body{
  width: 100%;
}

.about .container .mini-about,
.about .container .mini-contact{
  display: block;
  position: absolute;
  top: 10%;
  padding: 1rem;
  font-size: 3rem;
  cursor: pointer;
}

.about .container .mini-about{
  left: 0;
  transition: all 1s ease;
  border-radius: 0 15px 15px 0;
}

.about .container .mini-contact{
  right: 0;
  transition: all 1s ease;
  border-radius: 15px 0 0 15px;
}

.about .img-container{
  width: 21rem;
  margin: 0 auto;
}

.about .container img{
  max-width: 100%;
  max-height: 100%;
  border-radius: 100%;
  margin: 0 auto;
}

.about .container .description{
  margin-top: 1rem;
  font-size: 2rem;
  transition: all 5s ease;
  max-height: 100%;
}

.about .container .contact-description{
  color: #f6f6f6;
}

@media only screen and (max-width: 1380px) {

  .landing .container h2{
    font-size: 8rem;
  }

}

@media only screen and (max-width: 1100px) {
  :root{
    font-size: 9px;
  }

  .desktop{
    display: none;
  }

  .mobile{
    display: block;
  }

  .contact{
    left: 6%;
    right: 3%;
  }

  .projects{
    height: auto;
  }

  .projects .img-container{
    max-width: 75%;
  }

}

@media only screen and (max-width: 1000px) {

  .projects .img-container{
    max-width: 75%;
  }

}

@media only screen and (max-width: 950px) {

  .landing .container div{
    text-align: center;
  }

  .landing .container h2{
    font-size: 8rem;
  }

}

@media only screen and (max-width: 780px) {

  .landing .container h1{
    font-size: 2rem;
  }

  .landing .container h2{
    font-size: 7rem;
  }

}

@media only screen and (max-width: 620px) {

  .landing .container h1{
    font-size: 1.5rem;
  }

  .landing .container h2{
    font-size: 6rem;
  }

}

@media only screen and (max-width: 530px) {

  .landing .container h1{
    font-size: 1.3rem;
  }

  .landing .container h2{
    font-size: 5rem;
  }

}

@media only screen and (max-width: 450px) {

  .landing .container h1{
    font-size: 1.5rem;
  }


  .landing .container h2{
    font-size: 4rem;
  }

  .projects .img-container{
    max-width: 90%;
  }

}

@media only screen and (max-width: 360px) {

  .landing .container h1{
    font-size: 1.5rem;
  }


  .landing .container h2{
    font-size: 3.5rem;
  }

}

@media only screen and (max-height: 900px) {

  .mobile ul li{
    margin-top: 2.5rem;
  }

  .about{
    height: auto;
  }

}

@media only screen and (max-height: 655px) {

  .projects{
    height: auto;
  }
}

